import React, { useContext } from 'react';
import { ShopContext } from '../context/ShopContext';
import { Card, Button, Row, Col } from 'react-bootstrap';
import { Link, Navigate, useNavigate } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import './Home.css';





const Home = () => {
  
  const navigate = useNavigate();

  return (
    <div>
      <h1>Welcome to the Furniture World</h1>
      <Container>
      <Row>
        <Col>
            <Card style={{ width: '10rem' }}>
            <Card.Img variant="top" src="data:image/jpeg;base64," height={100}/>
            <Card.Body>
                <Card.Title>Living</Card.Title>
                    <Card.Text>
                    More Living Room Furniture click below the button
                    </Card.Text>
                <Button onClick={()=>navigate('/Living')} variant="primary">More</Button>
            </Card.Body>
            </Card>
        </Col>
        <Col>
        <Card style={{ width: '10rem' }}>
            <Card.Img variant="top" src="data:image/jpeg;base64," height={100}/>
            <Card.Body>
                <Card.Title>Bed</Card.Title>
                    <Card.Text>
                    More Bed Room Furniture click below the button
                    </Card.Text>
                <Button onClick={()=>navigate('/Bed')} variant="primary">More</Button>
            </Card.Body>
            </Card>
        </Col>
        <Col>
        <Card style={{ width: '10rem' }}>
            <Card.Img variant="top" src="data:image/jpeg;base64," height={100}/>
            <Card.Body>
                <Card.Title>Dining</Card.Title>
                    <Card.Text>
                    More Dining Room Furniture click below the button
                    </Card.Text>
                <Button onClick={()=>navigate('/Dining')} variant="primary">More</Button>
            </Card.Body>
            </Card>
        </Col>
        <Col>
        <Card style={{ width: '10rem' }}>
            <Card.Img variant="top" src="data:image/jpeg;base64," height={100}/>
            <Card.Body>
                <Card.Title>Office</Card.Title>
                    <Card.Text>
                    More Office Room Furniture click below the button
                    </Card.Text>
                <Button onClick={()=>navigate('/Office')} variant="primary">More</Button>
            </Card.Body>
            </Card>
        </Col>
        <Col>
            <Card style={{ width: '10rem' }}>
                <Card.Img variant="top" src="data:image/jpeg;base64," height={100}/>
                <Card.Body>
                    <Card.Title>Plastic</Card.Title>
                        <Card.Text>
                        More Plastic Furniture click below the button
                        </Card.Text>
                    <Button onClick={()=>navigate('/Plastic')} variant="primary">More</Button>
                </Card.Body>
                </Card>
        </Col>
      </Row>
    </Container>
      
    </div>
  );
};

export default Home;
