import React, { useContext } from 'react';
import { Card, Row, Col, Button } from 'react-bootstrap';

import { OrderContext } from './OrderContext';


const ViewProduct = () => {
  const { orders, updateOrder } = useContext(OrderContext);
  const filteredOrders = orders?.filter(order => order.quantity > 0) || [];
  console.log("ViewProduct component loaded");


  const handleRemove = (item) => {
    const confirmRemove = window.confirm(`Are you sure you want to remove "${item.title}"?`);
    if (confirmRemove) {
      updateOrder(item, 0);
      alert(`${item.title} removed from your order.`);
    }
  };

  const handleClearAll = () => {
    const confirmClear = window.confirm("Are you sure you want to clear all ordered items?");
    if (confirmClear) {
      filteredOrders.forEach(item => updateOrder(item, 0));
      alert("All items have been cleared.");
    }
  };

 
  return (
    <div className="container">
      <h2 className="mb-4">Your Ordered Products</h2>

      {filteredOrders.length === 0 ? (
        <p>No items have been ordered yet.</p>
      ) : (
        <>
          <Row>
            {filteredOrders.map((item) => (
              <Col key={item.id} md={4} className="mb-4">
                <Card style={{ width: '18rem' }}>
                  <Card.Img variant="top" src={item.img} height={200} />
                  <Card.Body>
                    <Card.Title>{item.title}</Card.Title>
                    <Card.Text>{item.description}</Card.Text>
                    <p><strong>Quantity:</strong> {item.quantity}</p>
                    <Button
                      variant="danger"
                      size="sm"
                      onClick={() => handleRemove(item)}
                    >
                      Remove
                    </Button>
                  </Card.Body>
                </Card>
              </Col>
            ))}
          </Row>

          <div className="mt-4 d-flex gap-3">
            <Button variant="outline-danger" onClick={handleClearAll}>
              Clear All Items
            </Button>
          
          </div>
        </>
      )}
    </div>
  );
};

export default ViewProduct;
