import React, { useContext, useState } from 'react';
import { Button } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import { OrderContext } from '../context/OrderContext';

const sofaData = [
  {
    id: 31,
    img: "https://www2.ad-on-line-store.com/wp-content/uploads/2021/10/tixfcb75-900.jpg",
    title: "Miami - Sofas",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 32,
    img: "https://www.cantori.it/public/images/674_cantori-miamidivano2.jpg",
    title: "Miami - Sofas",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 33,
    img: "https://image.architonic.com/man3-1/10002539/cantori-about-gabrielle-armachairs-small-table-atenae-painting-line-set-arcit18.jpg",
    title: "Miami - Sofas",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 34,
    img: "https://i.greatcatalog.net/v/8/8245/8245-353@2x.webp",
    title: "Miami - Sofas",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 35,
    img: "https://m.media-amazon.com/images/I/717XJNJ-CxS.jpg",
    title: "Miami - Sofas",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id:36,
    img: "https://homestylefurnishingsni.com/wp-content/uploads/2024/07/Untitled-design-41.jpg",
    title: "Large - Sofas",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  }
];

const Living = () => {
  const [quantities, setQuantities] = useState(Array(sofaData.length).fill(0));
  const {updateOrder}=useContext(OrderContext);

  const handleIncrease = (index) => {
    const updated = [...quantities];
    updated[index]++;
    setQuantities(updated);
    updateOrder(sofaData[index], updated[index]);
  };

  const handleDecrease = (index) => {
    const updated = [...quantities];
    if (updated[index] > 0) updated[index]--;
    setQuantities(updated);
    updateOrder(sofaData[index], updated[index]);
  };

  return (
    <div>
      <h1>This is Living Room's Furnitures</h1>
      <Row>
        {sofaData.map((sofa, index) => (
          <Col key={sofa.id} md={4} className="mb-4">
            <Card style={{ width: '18rem' }}>
              <Card.Img variant="top" src={sofa.img} height={225} />
              <Card.Body>
                <Card.Title>ID: {sofa.id}</Card.Title>
                <Card.Text>{sofa.description}</Card.Text>
              </Card.Body>
              <ListGroup className="list-group-flush">
                <ListGroup.Item>{sofa.title}</ListGroup.Item>
                <ListGroup.Item>Ordered No: {quantities[index]}</ListGroup.Item>
                <ListGroup.Item>
                  ADD{' '}
                  <ButtonGroup aria-label="Basic example">
                    <Button variant="primary" onClick={() => handleIncrease(index)}>+</Button>
                    <Button variant="secondary" onClick={() => handleDecrease(index)}>-</Button>
                  </ButtonGroup>
                </ListGroup.Item>
              </ListGroup>
            </Card>
          </Col>
        ))}
      </Row>
    </div>
  );
};

export default Living;
