import React, { useContext, useState } from 'react';
import { Button } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import { OrderContext } from '../context/OrderContext';

const DiningData = [
  {
    id: 21,
    img: "https://damro.lk/wp-content/uploads/2023/11/13.png",
    title: "Medium - Table",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 22,
    img: "https://www.talsmafurniture.com/wp-content/uploads/2022/12/Designing-a-Dining-Room-Where-to-Start.jpg",
    title: "Large - Table",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 23,
    img: "https://m.media-amazon.com/images/I/A1mxxEAX+zL.jpg",
    title: "Large - Table",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 24,
    img: "https://image.made-in-china.com/202f0j00DbToacIEOJqH/Modern-Home-Furniture-Dining-Table-Set-6-Seater-Round-Dining-Table.jpg",
    title: "Small - Table",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 25,
    img: "https://images-cdn.ubuy.com.sa/6620665b8a061f217e1981cd-ipormis-7-pieces-dining-table-chairs.jpg",
    title: "Medium - Table",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 26,
    img: "https://belfurniture.com/cdn/shop/products/9-piece-dining-room-set-987188.jpg?v=1695064808",
    title: "Large - Table",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  }
];

const Dining = () => {
  const [quantities, setQuantities] = useState(Array(DiningData.length).fill(0));
  const {updateOrder}= useContext(OrderContext);

  const handleIncrease = (index) => {
    const updated = [...quantities];
    updated[index]++;
    setQuantities(updated);
    updateOrder(DiningData[index], updated[index]);
  };

  const handleDecrease = (index) => {
    const updated = [...quantities];
    if (updated[index] > 0) updated[index]--;
    setQuantities(updated);
    updateOrder(DiningData[index], updated[index]);
  };

  return (
    <div>
      <h1>This is Dining Room's Furnitures</h1>
      <Row>
        {DiningData.map((Dining, index) => (
          <Col key={Dining.id} md={4} className="mb-4">
            <Card style={{ width: '18rem' }}>
              <Card.Img variant="top" src={Dining.img} height={225} />
              <Card.Body>
                <Card.Title>ID: {Dining.id}</Card.Title>
                <Card.Text>{Dining.description}</Card.Text>
              </Card.Body>
              <ListGroup className="list-group-flush">
                <ListGroup.Item>{Dining.title}</ListGroup.Item>
                <ListGroup.Item>Ordered No: {quantities[index]}</ListGroup.Item>
                <ListGroup.Item>
                  ADD{' '}
                  <ButtonGroup aria-label="Basic example">
                    <Button variant="primary" onClick={() => handleIncrease(index)}>+</Button>
                    <Button variant="secondary" onClick={() => handleDecrease(index)}>-</Button>
                  </ButtonGroup>
                </ListGroup.Item>
              </ListGroup>
            </Card>
          </Col>
        ))}
      </Row>
    </div>
  );
};

export default Dining;
