import React, { useContext, useState } from 'react';
import { Button } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import { OrderContext } from '../context/OrderContext';


const OfficeData = [
  {
    id: 41,
    img: "https://www.furnituresrilanka.com/wp-content/uploads/2017/11/office-furniture-sri-lanka-new-22-1024x705.jpg",
    title: "Medium - Table",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 42,
    img: "https://angiehomes.co/cdn/shop/products/Zuko-Director-Table-ANGIE-HOMES-1613976749_grande.jpg?v=1613976751",
    title: "Large - Table",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 43,
    img: "https://rnb.scene7.com/is/image/roomandboard/ESP_parsons_247860_23e?size=2400,2400&scl=1",
    title: "Small - Table",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 44,
    img: "https://www.bushhome.com/cdn/shop/files/38a54c73bf20a5ba374a8137dc9b3278d0638575.png?v=1741005249&width=3000",
    title: "Small -Table",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 45,
    img: "https://www.avantisystemsusa.com/wp-content/uploads/2021/06/storage.jpg",
    title: "Small - Table",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 46,
    img: "https://assets.bdiusa.com/Misc-Images-Files/Blog-Images/_1440x810_crop_center-center_none/centro-office-linea-shelves-bdi-white-office-furniture-picking-perfect-1440.jpg?v=1724335338",
    title: "Medium - Table",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  }
];

const Office = () => {
  const [quantities, setQuantities] = useState(Array(OfficeData.length).fill(0));
  const {updateOrder}=useContext(OrderContext);

  const handleIncrease = (index) => {
    const updated = [...quantities];
    updated[index]++;
    setQuantities(updated);
    updateOrder(OfficeData[index], updated[index]);
  };

  const handleDecrease = (index) => {
    const updated = [...quantities];
    if (updated[index] > 0) updated[index]--;
    setQuantities(updated);
    updateOrder(OfficeData[index], updated[index]);
  };

  return (
    <div>
      <h1>This is Office Room's Furnitures</h1>
      <Row>
        {OfficeData.map((table, index) => (
          <Col key={table.id} md={4} className="mb-4">
            <Card style={{ width: '18rem' }}>
              <Card.Img variant="top" src={table.img} height={225} />
              <Card.Body>
                <Card.Title>ID: {table.id}</Card.Title>
                <Card.Text>{table.description}</Card.Text>
              </Card.Body>
              <ListGroup className="list-group-flush">
                <ListGroup.Item>{table.title}</ListGroup.Item>
                <ListGroup.Item>Ordered No: {quantities[index]}</ListGroup.Item>
                <ListGroup.Item>
                  ADD{' '}
                  <ButtonGroup aria-label="Basic example">
                    <Button variant="primary" onClick={() => handleIncrease(index)}>+</Button>
                    <Button variant="secondary" onClick={() => handleDecrease(index)}>-</Button>
                  </ButtonGroup>
                </ListGroup.Item>
              </ListGroup>
            </Card>
          </Col>
        ))}
      </Row>
    </div>
  );
};

export default Office;
