import React, { useContext, useState } from 'react';
import { Button } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import { OrderContext } from '../context/OrderContext';

const PlasticData = [
  {
    id: 51,
    img: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcReaKYCvadc4EvoOPFKk7LuZ5DYwVJm4rpMp2uR6DzyOebWsT-xfumaxMcJwE1lThlokMs&usqp=CAU",
    title: "Plastic - Chairs",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 52,
    img: "https://m.media-amazon.com/images/I/81AQwYlHlKL._AC_SL1500_.jpg",
    title: "Plastic - Chairs",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 53,
    img: "https://mangochairs.com/wp-content/uploads/2022/10/IMG-20220105-WA0077__01.jpg",
    title: "Plastic - Chairs",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 54,
    img: "https://m.media-amazon.com/images/I/61YOETfm-XL.jpg",
    title: "Plastic - Chairs",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 55,
    img: "https://c8.alamy.com/comp/HBFEDR/plastic-furniture-HBFEDR.jpg",
    title: "Plastic - Chairs",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 56,
    img: "https://static.dezeen.com/uploads/2018/11/Luken-recycled-plastic-furniture_dezeen_hero2-1704x959.jpg",
    title: "Plastic - Chairs",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  }
];

const Plastic = () => {
  const [quantities, setQuantities] = useState(Array(PlasticData.length).fill(0));
  const {updateOrder}=useContext(OrderContext);

  const handleIncrease = (index) => {
    const updated = [...quantities];
    updated[index]++;
    setQuantities(updated);
    updateOrder(PlasticData[index], updated[index]);
  };

  const handleDecrease = (index) => {
    const updated = [...quantities];
    if (updated[index] > 0) updated[index]--;
    setQuantities(updated);
    updateOrder(PlasticData[index], updated[index]);
  };

  return (
    <div>
      <h1>This is Plastic Furnitures</h1>
      <Row>
        {PlasticData.map((pla, index) => (
          <Col key={pla.id} md={4} className="mb-4">
            <Card style={{ width: '18rem' }}>
              <Card.Img variant="top" src={pla.img} height={225} />
              <Card.Body>
                <Card.Title>ID: {pla.id}</Card.Title>
                <Card.Text>{pla.description}</Card.Text>
              </Card.Body>
              <ListGroup className="list-group-flush">
                <ListGroup.Item>{pla.title}</ListGroup.Item>
                <ListGroup.Item>Ordered No: {quantities[index]}</ListGroup.Item>
                <ListGroup.Item>
                  ADD{'  '}
                  <ButtonGroup aria-label="Basic example">
                    <Button variant="primary" onClick={() => handleIncrease(index)}>+</Button>
                    <Button variant="secondary" onClick={() => handleDecrease(index)}>-</Button>
                  </ButtonGroup>
                </ListGroup.Item>
              </ListGroup>
            </Card>
          </Col>
        ))}
      </Row>
    </div>
  );
};

export default Plastic;
