import React, { useContext, useState } from 'react';
import { Button } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import { OrderContext } from '../context/OrderContext';

const BedData = [
  {
    id: 11,
    img: "https://damro.lk/wp-content/uploads/2024/08/Lora-Bedroom-Suite-2-Web.jpg",
    title: "Single - Bed",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 12,
    img: "https://finez.lk/cdn/shop/products/Ardly.png?v=1646289996",
    title: "Double - Bed",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 13,
    img: "https://cdn11.bigcommerce.com/s-1u1m3wn/images/stencil/960w/products/4630/24272/Aesha_dresser_suite_lifestyle_front_on__34296.1734923072.jpg?c=2",
    title: "Single - Bed",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 14,
    img: "https://asianlanka.lk/wp-content/uploads/2020/09/camilia.jpg",
    title: "MFD - Bed",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 15,
    img: "https://aca8cd9d105dbd447097-f6f51e4cef559c9308eef9d726fd38a7.ssl.cf1.rackcdn.com/74754-1.jpg",
    title: "Triple - Bed",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  },
  {
    id: 16,
    img: "https://aca8cd9d105dbd447097-f6f51e4cef559c9308eef9d726fd38a7.ssl.cf1.rackcdn.com/773830-2.jpg",
    title: "Triple - Bed",
    description: "Some quick example text to build on the card title and make up the bulk of the card's content."
  }
];

const Bed = () => {
  const [quantities, setQuantities] = useState(Array(BedData.length).fill(0));
  const {updateOrder} = useContext(OrderContext)

  const handleIncrease = (index) => {
    const updated = [...quantities];
    updated[index]++;
    setQuantities(updated);
    updateOrder(BedData[index],updated[index]);
  };

  const handleDecrease = (index) => {
    const updated = [...quantities];
    if (updated[index] > 0) updated[index]--;
    setQuantities(updated);
    updateOrder(BedData[index],updated[index]);
  };

  return (
    <div>
      <h1>This is Bed Room's Furnitures</h1>
      <Row>
        {BedData.map((Bed, index) => (
          <Col key={Bed.id} md={4} className="mb-4">
            <Card style={{ width: '18rem' }}>
              <Card.Img variant="top" src={Bed.img} height={225} />
              <Card.Body>
                <Card.Title>ID: {Bed.id}</Card.Title>
                <Card.Text>{Bed.description}</Card.Text>
              </Card.Body>
              <ListGroup className="list-group-flush">
                <ListGroup.Item>{Bed.title}</ListGroup.Item>
                <ListGroup.Item>Ordered No: {quantities[index]}</ListGroup.Item>
                <ListGroup.Item>
                  ADD{' '}
                  <ButtonGroup aria-label="Basic example">
                    <Button variant="primary" onClick={() => handleIncrease(index)}>+</Button>
                    <Button variant="secondary" onClick={() => handleDecrease(index)}>-</Button>
                  </ButtonGroup>
                </ListGroup.Item>
              </ListGroup>
            </Card>
          </Col>
        ))}
      </Row>
    </div>
  );
};

export default Bed;
